export default "/* Not a valid style option ratio, but used as the mobile base for 8x1 and 20x1 */\n\n.message__content {\n    display: block;\n    padding: 0.25rem;\n}\n\n.message__logo-container {\n    width: 40%;\n    display: block;\n    margin-bottom: 0.25rem;\n}\n\n.message__logo {\n    width: 100%;\n}\n\n.message__messaging {\n    flex-direction: row;\n    align-items: flex-end;\n}\n\n.message__headline {\n    margin-bottom: 0;\n    margin-right: 0.25rem;\n    font-size: 4vw;\n}\n\n.message__promo-container {\n    flex: 1 1 100%;\n}\n\n.message__disclaimer {\n    flex: 1 1 auto;\n    width: auto;\n    padding-right: 0;\n    margin-top: 1vw;\n    display: block;\n}\n\n.message__disclaimer span.multi:nth-of-type(1) {\n    display: none;\n}\n\n@media (max-aspect-ratio: 61/10) and (min-width: 400px) {\n    .message__content {\n        padding: 0.75rem;\n    }\n}\n\n@media (max-aspect-ratio: 61/10) and (min-width: 520px) {\n    .message__disclaimer span.multi:nth-of-type(1) {\n        display: inline;\n    }\n}\n";